import { Button, Form, Modal, Radio, Space } from "antd";
import Typography from "antd/lib/typography";
import img from "assets/icons/common/merge.png";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import { getRecords, mergeRecord } from "redux/slices/objects";
import styled from "styled-components";
import ModalConfirm from "./ModalConfirm";
const { Text } = Typography;

const ModalMerge = (props) => {
  const {
    openMerge,
    setOpenMerge,
    selectedRowKeys,
    methodData,
    resetSelectedRowKeys,
  } = props;
  const [openConfirm, setOpenConfirm] = useState(false);
  const dispatch = useDispatch();
  const { objectId } = useParams();
  const [form] = Form.useForm();

  const { recordToMerge, isLoading, userAssignTo } = useSelector(
    (state) => state.objectsReducer
  );

  const handleOk = () => {
    setOpenMerge(false);
  };

  const handleCancel = () => {
    setOpenMerge(false);
  };

  useEffect(() => {
    if (isLoading === false) {
      // dispatch(setShowModalConfirm(false));
      resetSelectedRowKeys();
      setOpenConfirm(false);
      setOpenMerge(false);
      form.resetFields();
    }
    // eslint-disable-next-line
  }, [isLoading]);

  useEffect(() => {
    if (openMerge === true) {
      dispatch(
        getRecords({
          ids: selectedRowKeys,
          object_id: objectId,
        })
      );
    }
    // eslint-disable-next-line
  }, [openMerge]);

  const [dataMethod, setDataMethod] = useState({});

  useEffect(() => {
    let newObj = {};
    Object.entries(
      (recordToMerge &&
        recordToMerge.data &&
        recordToMerge.data.length > 0 &&
        recordToMerge.data[0]) ||
        {}
      // eslint-disable-next-line
    ).forEach(([key, value], idx) => {
      if (typeof value === "object") {
        if (
          recordToMerge &&
          recordToMerge.data &&
          recordToMerge.data.length === 2
        ) {
          if (
            recordToMerge &&
            recordToMerge.data &&
            recordToMerge.data.length > 0 &&
            recordToMerge.data[0][key]?.value
          ) {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[0][key]),
            });
          } else if (
            recordToMerge &&
            recordToMerge.data &&
            recordToMerge.data.length > 0 &&
            recordToMerge.data[1][key]?.value
          ) {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[1][key]),
            });
          } else {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[0][key]),
            });
          }
        }
        if (
          recordToMerge &&
          recordToMerge.data &&
          recordToMerge.data.length === 3
        ) {
          if (
            recordToMerge &&
            recordToMerge.data &&
            recordToMerge.data.length > 0 &&
            recordToMerge.data[0][key]?.value
          ) {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[0][key]),
            });
          } else if (
            recordToMerge &&
            recordToMerge.data &&
            recordToMerge.data.length > 0 &&
            recordToMerge.data[1][key]?.value
          ) {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[1][key]),
            });
          } else if (
            recordToMerge &&
            recordToMerge.data &&
            recordToMerge.data.length > 0 &&
            recordToMerge.data[2][key]?.value
          ) {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[2][key]),
            });
          } else {
            newObj[key] = JSON.stringify({
              id_field: key,
              ...(recordToMerge &&
                recordToMerge.data &&
                recordToMerge.data.length > 0 &&
                recordToMerge.data[0][key]),
            });
          }
        }
      }
    });
    newObj["assignTo"] =
      recordToMerge &&
      recordToMerge.data &&
      recordToMerge.data.length > 0 &&
      recordToMerge.data[0]["owner"];

    newObj["id"] =
      recordToMerge &&
      recordToMerge.data &&
      recordToMerge.data.length > 0 &&
      recordToMerge.data[0]["_id"];

    form.setFieldsValue(newObj);
    // eslint-disable-next-line
  }, [recordToMerge]);

  const onFinish = (values) => {
    let arr = [];
    // eslint-disable-next-line
    Object.entries(values).forEach(([key, value], index) => {
      if (key !== "assignTo" && key !== "id") {
        arr.push(JSON.parse(value));
      }
    });
    let slaves = selectedRowKeys.filter((item) => item !== values["id"]);
    setDataMethod({
      fields: arr,
      masterID: values["id"],
      object_id: objectId,
      owner: values["assignTo"],
      slaves: slaves,
    });
    // dispatch(setShowModalConfirm(true));
    setOpenConfirm(true);
  };

  return (
    <>
      <CustomModal
        title="Gp bn ghi"
        visible={openMerge}
        onOk={handleOk}
        onCancel={handleCancel}
        width={960}
        footer={false}
      >
        <div className="content">
          Khi hp nht cc bn ghi, bn ghi chnh c cp nht vi cc trng
          c chn. Tt c cc danh sch lin quan, cc mc ngun cp d liu
          v cc bn ghi con c hp nht vo cc bn ghi chnh.
        </div>
        <div style={{ marginTop: "20px", overflowX: "scroll", width: "100%" }}>
          <Form
            form={form}
            onFinish={onFinish}
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 16 }}
            colon={false}
            labelAlign="left"
            onValuesChange={(value, values) => {}}
          >
            <Header label=" " style={{ width: "100%" }}>
              {selectedRowKeys.map((item, idx) => {
                return <Title>Record {idx}</Title>;
              })}
            </Header>
            {recordToMerge &&
              recordToMerge.fields &&
              recordToMerge.fields.map((item, idx) => {
                return (
                  <FormCustom label={`${item.name}`} name={item.ID}>
                    <Radio.Group
                      onChange={(e) => {
                        form.setFieldsValue({
                          [item.ID]: e.target.value,
                        });
                      }}
                    >
                      <Space direction="horizontal">
                        {recordToMerge &&
                          recordToMerge.data &&
                          recordToMerge.data.map((record, index) => {
                            if (
                              item.type === "user" &&
                              record[item.ID] &&
                              record[item.ID].value
                            ) {
                              return (
                                <Radio
                                  value={JSON.stringify({
                                    id_field: item.ID,
                                    ...record[item.ID],
                                  })}
                                >
                                  <Text
                                    ellipsis={{
                                      tooltip: record[item.ID].fullname,
                                    }}
                                    style={{ width: "150px" }}
                                  >
                                    {record[item.ID].fullname}
                                  </Text>
                                </Radio>
                              );
                            } else
                              return (
                                <Radio
                                  value={JSON.stringify({
                                    id_field: item.ID,
                                    ...record[item.ID],
                                  })}
                                >
                                  <Text
                                    ellipsis={{
                                      tooltip:
                                        record[item.ID] &&
                                        record[item.ID].value,
                                    }}
                                    style={{ width: "150px" }}
                                  >
                                    {record[item.ID] && record[item.ID].value}
                                  </Text>
                                </Radio>
                              );
                          })}
                      </Space>
                    </Radio.Group>
                  </FormCustom>
                );
              })}
            <FormCustom label="Assign to" name="assignTo">
              <Radio.Group
                onChange={(e) => {
                  form.setFieldsValue({
                    owner: e.target.value,
                  });
                }}
              >
                <Space direction="horizontal">
                  {recordToMerge &&
                    recordToMerge.data &&
                    recordToMerge.data.map((record, index) => {
                      let name = userAssignTo.filter(
                        (item) => item._id === record["owner"]
                      );
                      let fullName =
                        name.length > 0
                          ? `${name[0].First_Name} ${name[0].Middle_Name} ${name[0].Last_Name}`
                          : "";
                      return (
                        <Radio value={record["owner"]} key={record["owner"]}>
                          <Text
                            ellipsis={{ tooltip: fullName }}
                            style={{ width: "150px" }}
                          >
                            {fullName}
                          </Text>
                        </Radio>
                      );
                    })}
                </Space>
              </Radio.Group>
            </FormCustom>

            <FormCustom label="Bn ghi" name="id">
              <Radio.Group
                onChange={(e) => {
                  form.setFieldsValue({
                    owner: e.target.value,
                  });
                }}
              >
                <Space direction="horizontal">
                  {recordToMerge &&
                    recordToMerge.data &&
                    recordToMerge.data.map((record, index) => {
                      return (
                        <Radio value={record["_id"]} key={record["_id"]}>
                          <Text
                            ellipsis={{ tooltip: `Record ${index}` }}
                            style={{ width: "150px" }}
                          >
                            Record {index}
                          </Text>
                        </Radio>
                      );
                    })}
                </Space>
              </Radio.Group>
            </FormCustom>
            <CustomFooter>
              <CustomButtonSave
                size="large"
                htmlType="submit"
                isLoading={isLoading}
              >
                Save
              </CustomButtonSave>
              <CustomButtonCancel
                size="large"
                onClick={() => {
                  setOpenMerge(false);
                }}
              >
                Cancel
              </CustomButtonCancel>
            </CustomFooter>
          </Form>

          {openMerge ? (
            <ModalConfirm
              openConfirm={openConfirm}
              setOpenConfirm={setOpenConfirm}
              title={"Bn c chc chn thay i cc bn ghi ny?"}
              decs={"Sau khi lu bn ghi, d liu s khng th hon tc."}
              method={mergeRecord}
              data={{
                data: dataMethod,
                methodData,
              }}
              // setOpenModal={setOpenConfirm}
              img={img}
            />
          ) : (
            ""
          )}
        </div>
      </CustomModal>
    </>
  );
};

export default ModalMerge;

