import { CaretDownOutlined, CaretUpOutlined } from "@ant-design/icons";
import {
  Button,
  Drawer,
  Input,
  Radio,
  Select,
  Spin,
  Typography,
  Tooltip,
  Form,
} from "antd";
import React, { useEffect, useState } from "react";
import CRMLogo from "assets/icons/common/crmlogo.png";
import LeftPagi from "assets/icons/objects/pagi-left.png";
import RightPagi from "assets/icons/objects/pagi-right.png";
import Reload from "assets/icons/objects/reload.png";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import {
  loadDataLinking,
  loadPagination,
  linkingGlobalSearch,
} from "redux/slices/objects";
import styled from "styled-components";
const { Option } = Select;
const { Text: TextComponent } = Typography;
const { Search } = Input;
const DrawerLookup = (props) => {
  const {
    visible,
    setVisible,
    selectedRowKeys,
    setSelectedRowKeys,
    field,
    handleSelect,
    selectedRowKeysOutside,
    setSelectedRows,
    rowData,
    setRowData,
    // selectedRows,
  } = props;

  const onClose = () => {
    setVisible(false);
  };

  const { t } = useTranslation();

  const [recordPerPage] = useState(50);
  const [currentPage, setCurrentPage] = useState(1);

  const dispatch = useDispatch();

  const {
    headerLinking,
    dataLinking,
    recordData,
    isLoadingDataLinking,
    isLoadingPagi,
    totalRecord,
    objectLinkingGlobalSearch,
  } = useSelector((state) => state.objectsReducer);

  const [listSearch, setListSearch] = useState([]);
  const [source, setSource] = useState([]);

  const [sortBy, setSortBy] = useState({});
  const { defaultBrandName } = useSelector((state) => state.brandNameReducer);

  const [firstID, setFirstID] = useState(null);
  const [lastID, setLastID] = useState(null);

  const [next, setNext] = useState(1);
  const [prev, setPrev] = useState(1);
  const [notiGlobal, setNotiGlobal] = useState("");
  const [formLookup] = Form.useForm();
  const [isSearch, setIsSearch] = useState(false);

  useEffect(() => {
    formLookup.resetFields();
    setIsSearch(false);
    //eslint-disable-next-line
  }, [visible]);

  useEffect(() => {
    if (Object.entries(objectLinkingGlobalSearch).length > 0) {
      if (objectLinkingGlobalSearch.searchable_fields.length > 0) {
        let noti = "Only ";
        objectLinkingGlobalSearch.searchable_fields.forEach((field, index) => {
          noti += `${
            index === objectLinkingGlobalSearch.searchable_fields.length - 1
              ? field.name
              : `${field.name},`
          } `;
        });
        noti += `${
          objectLinkingGlobalSearch.searchable_fields.length > 2 ? "are" : "is"
        } searchable.`;
        setNotiGlobal(noti);
      } else {
        setNotiGlobal(
          "Global search is not available. Please contact the administrator to index data and enable searching."
        );
      }
    }
  }, [objectLinkingGlobalSearch]);

  function numberWithCommas(x, col) {
    if (x === 0) {
      return "0";
    } else
      return x
        .toFixed(col?.decimal_separator === 0 ? 0 : col?.decimal_separator || 3)
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  useEffect(() => {
    let newData = [];

    dataLinking.forEach((record) => {
      let newRecord = {};
      newRecord["_id"] = record._id;

      headerLinking.forEach((col) => {
        let flag = 0;
        Object.entries(record).forEach(([key, value]) => {
          if (col.ID === key) {
            flag = 1;
            if (typeof value === "object" && value !== null) {
              newRecord[key] = value.value;
            } else {
              newRecord[key] = value;
            }
          }
        });
        if (flag === 0) {
          newRecord[col.ID] = null;
        }
      });
      newData.push(newRecord);
    });

    setSource(newData);
  }, [dataLinking, headerLinking]);

  const reloadData = (searchData, firstID, lastID, sortBy) => {
    let dataFilter = [];
    /* eslint-disable-next-line */
    // Object.entries(dataFilterLinking).forEach(([key, value], index) => {
    //   if (value && value.id_field_related_record !== null) {
    //     dataFilter.push(value);
    //   }
    // });
    let meta = [];
    let data = [];
    // eslint-disable-next-line
    searchData.map((item, idx) => {
      if (
        item.id_field === "created_date" ||
        item.id_field === "created_by" ||
        item.id_field === "modify_time" ||
        item.id_field === "owner" ||
        item.id_field === "modify_by"
      ) {
        meta.push(item);
      } else {
        data.push(item);
      }
    });
    dispatch(
      loadDataLinking({
        object_id: field.objectname,
        first_record_id: firstID,
        last_record_id: lastID,
        search_with: {
          meta: meta,
          data: data,
        },
        // sort_by: sortBy,
        filter: dataFilter,
      })
    );
    // dispatch(
    //   loadPaginationLinking({
    //     object_id: field.objectname,
    //     current_page: currentPage,
    //     record_per_page: recordPerPage,
    //     search_with: {
    //       meta: meta,
    //       data: data,
    //     },
    //     filter: dataFilter,
    //   })
    // );
  };

  const handleSortNew = (sortTmp) => {
    reloadData(listSearch, firstID, lastID, sortTmp);
  };

  const handldeChangeListSearch = (id, value) => {
    let temp = [...listSearch];
    if (value) {
      let flag = 0;
      temp.forEach((item, index) => {
        if (item.id_field === id) {
          flag = 1;
          temp[index].value = value;
        }
      });
      if (flag === 0) {
        temp.push({ id_field: id, value: value });
      }
    } else {
      temp.forEach((item, index) => {
        if (item.id_field === id) {
          temp.splice(index, 1);
        }
      });
    }

    if (!value) {
      reloadData(temp, firstID, lastID, sortBy);
    }
    setListSearch(temp);
  };

  const handleSearchNew = (id, value) => {
    let temp = [...listSearch];
    if (value) {
      let flag = 0;
      temp.forEach((item, index) => {
        if (item.id_field === id) {
          flag = 1;
          temp[index].value = value;
        }
      });
      if (flag === 0) {
        temp.push({ id_field: id, value: value });
      }
    } else {
      temp.forEach((item, index) => {
        if (item.id_field === id) {
          temp.splice(index, 1);
        }
      });
    }

    setListSearch(temp);

    reloadData(temp, firstID, lastID, sortBy);
  };

  const renderFieldFile = (value) => {
    /* eslint-disable-next-line */
    const expression =
      /*eslint-disable-next-line*/
      /[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]+)?/gi;
    const regex = new RegExp(expression);
    if (value) {
      /*eslint-disable-next-line*/
      return value.map((item, index) => {
        if (item !== null && item.match(regex)) {
          let fileNew = item.split("/");
          return (
            <a
              key={index}
              style={{ display: "block" }}
              href={item}
              target={"_blank"}
              rel="noreferrer"
            >
              {fileNew[fileNew.length - 1]}
            </a>
          );
        }
      });
    }
  };

  const mapRecordbyId = (id) => {
    let record = {};
    dataLinking.forEach((item) => {
      if (item._id === id) {
        Object.entries(item).forEach(([key, value]) => {
          if (value !== null && typeof value === "object") {
            record[key] = value.value;
          } else {
            record[key] = value;
          }
        });
      }
    });
    return record;
  };

  useEffect(() => {
    setNext(
      source.length === recordPerPage
        ? currentPage * recordPerPage
        : (currentPage - 1) * recordPerPage + source.length
    );
    /* eslint-disable-next-line */
  }, [source]);

  useEffect(() => {
    setPrev(currentPage * recordPerPage - recordPerPage + 1);
    /* eslint-disable-next-line */
  }, [currentPage]);

  const renderTable = () => {
    return (
      <table>
        <thead className="table-header">
          <tr>
            <th></th>
            {headerLinking.map((item, index) => {
              return (
                <th key={index}>
                  <ContentHeader>
                    <TitleWrap>
                      <span>{item.name}</span>
                      <IconHeader
                        style={{ display: "none" }}
                        className="hidden__component"
                      >
                        <CaretUpOutlined
                          style={{
                            color: `${
                              sortBy[item.ID] === 1
                                ? defaultBrandName.theme_color
                                : "rgba(0, 0, 0, 0.25)"
                            }`,
                          }}
                          onClick={() => {
                            let sortTmp = { ...sortBy };
                            if (sortTmp[item.ID] === 1) {
                              delete sortTmp[item.ID];
                            } else {
                              sortTmp[item.ID] = 1;
                            }
                            handleSortNew(sortTmp);
                            setSortBy(sortTmp);
                          }}
                        />
                        <CaretDownOutlined
                          style={{
                            color: `${
                              sortBy[item.ID] === -1
                                ? defaultBrandName.theme_color
                                : "rgba(0, 0, 0, 0.25)"
                            }`,
                          }}
                          onClick={() => {
                            let sortTmp = { ...sortBy };
                            if (sortTmp[item.ID] === -1) {
                              delete sortTmp[item.ID];
                            } else {
                              sortTmp[item.ID] = -1;
                            }
                            handleSortNew(sortTmp);
                            setSortBy(sortTmp);
                          }}
                        />
                      </IconHeader>
                    </TitleWrap>
                    <SearchWrap
                      isGlobal={objectLinkingGlobalSearch.allow_global_search}
                      className="hidden__component"
                    >
                      {item.type !== "select" ? (
                        <CustomSearch
                          disabled={
                            objectLinkingGlobalSearch.allow_global_search
                          }
                          value={
                            listSearch.find((val) => val.id_field === item.ID)
                              ?.value || ""
                          }
                          onChange={(value) => {
                            handldeChangeListSearch(
                              item.ID,
                              value.target.value
                            );
                          }}
                          allowClear={true}
                          onPressEnter={(value) => {
                            handleSearchNew(item.ID, value.target.value);
                          }}
                          placeholder={`${t("common.search")} ${item.name}`}
                        />
                      ) : (
                        <CustomSelect
                          disabled={
                            objectLinkingGlobalSearch.allow_global_search
                          }
                          value={
                            listSearch.find((val) => val.id_field === item.ID)
                              ?.value || undefined
                          }
                          showSearch
                          allowClear={true}
                          placeholder={`Search ${item.name}`}
                          onChange={(value) => {
                            handleSearchNew(item.ID, value);
                          }}
                          style={{ marginBottom: 8, display: "block" }}
                        >
                          {item &&
                            item.option &&
                            item.option.map((op, index) => {
                              return (
                                <Option key={index} value={op.label}>
                                  {op.label}
                                </Option>
                              );
                            })}
                        </CustomSelect>
                      )}
                    </SearchWrap>
                  </ContentHeader>
                </th>
              );
            })}
          </tr>
        </thead>
        <tbody className="table-body">
          {source.map((record, index) => {
            return (
              <tr key={index}>
                <CustomTd check={selectedRowKeys[0] === record._id}>
                  <Radio
                    disabled={
                      selectedRowKeysOutside
                        ? selectedRowKeysOutside.indexOf(record._id) < 0
                          ? false
                          : true
                        : recordData && recordData._id === record._id
                        ? true
                        : false
                    }
                    checked={selectedRowKeys[0] === record._id}
                    onChange={() => {
                      setSelectedRowKeys([record._id]);
                      setSelectedRows([mapRecordbyId(record._id)]);
                      setRowData(record);
                    }}
                  ></Radio>
                </CustomTd>
                {headerLinking.map((col) => {
                  // eslint-disable-next-line
                  return Object.entries(record).map(([key, value]) => {
                    if (col.ID === key) {
                      return (
                        <CustomTd
                          style={{
                            textAlign: `${
                              col.type === "number" ||
                              (col.type === "formula" &&
                                col.formula_type === "advanceExpression")
                                ? "right"
                                : "left"
                            }`,
                          }}
                          key={index}
                          check={selectedRowKeys[0] === record._id}
                        >
                          {typeof value === "object" ? (
                            <TextComponent ellipsis={{ tooltip: value }}>
                              {renderFieldFile(value)}
                            </TextComponent>
                          ) : (
                            <TextComponent ellipsis={{ tooltip: value }}>
                              {/* {value} */}
                              {col.type === "number" ||
                              (col.type === "formula" &&
                                col.formula_type === "advanceExpression")
                                ? numberWithCommas(value, col)
                                : value}
                            </TextComponent>
                          )}
                        </CustomTd>
                      );
                    }
                  });
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    );
  };
  const handleGlobalSearch = (value) => {
    if (value.trim().length > 100) {
      Notification("warning", "Ch tm kim ti a 100 k t!");
    } else {
      setCurrentPage(1);
      if (value.trim()) {
        setIsSearch(true);
        dispatch(
          linkingGlobalSearch({
            object_id: field.objectname,
            search_text: value.trim(),
          })
        );
      } else {
        setIsSearch(false);
        dispatch(
          loadDataLinking({
            object_id: field.objectname,
            first_record_id: null,
            last_record_id: null,
            search_with: {
              meta: [],
              data: [],
            },
            // sort_by: sortBy,
            filter: [],
          })
        );
      }
    }
  };
  return (
    <>
      <Drawer
        title="Lookup list"
        placement="right"
        onClose={onClose}
        visible={visible}
        width={1000}
        maskClosable={false}
      >
        <Wrapper>
          <div>
            {/* <CustomPagination
              showQuickJumper
              current={currentPage}
              total={totalRecordLinking}
              showSizeChanger
              showTotal={(total, range) =>
                `${range[0]}-${range[1]} of ${total} records`
              }
              // pageSize={recordPerPage}
              onChange={(e, pageSize) => {
                setCurrentPage(e);
                setRecordPerPage(pageSize);
                reloadData(listSearch, e, pageSize, sortBy);
              }}
            /> */}
            {objectLinkingGlobalSearch.allow_global_search && (
              <Form form={formLookup}>
                <Tooltip
                  overlayInnerStyle={{
                    backgroundColor: notiGlobal.includes("not available")
                      ? "#d4b106"
                      : "rgba(0, 0, 0, 0.75)",
                  }}
                  trigger="hover"
                  placement="bottom"
                  title={notiGlobal}
                >
                  <Form.Item name="test_name">
                    <CustomInputSearch
                      allowClear
                      onSearch={(value) => {
                        handleGlobalSearch(value);
                      }}
                      placeholder="Search this list..."
                    />
                  </Form.Item>
                </Tooltip>
              </Form>
            )}

            {!isSearch && (
              <NewCustomPagination>
                <>
                  {isLoadingDataLinking ? (
                    <Spin />
                  ) : (
                    <div className="total-record">
                      {next === 0 ? 0 : prev} - {next}{" "}
                      {isLoadingPagi ? (
                        <Spin style={{ marginLeft: "10px" }} />
                      ) : totalRecord === null ? (
                        <div
                          className="reload-pagi"
                          onClick={() => {
                            dispatch(
                              loadPagination({
                                current_page: 1,
                                record_per_page: 50,
                                object_id: field.objectname,
                              })
                            );
                          }}
                        >
                          <img alt="" src={Reload} />
                        </div>
                      ) : (
                        `of ${totalRecord ? totalRecord : 0} records`
                      )}
                    </div>
                  )}
                </>

                <div
                  className="left-pagi"
                  style={{
                    pointerEvents: `${currentPage === 1 ? "none" : ""}`,
                    cursor: `${currentPage === 1 ? "not-allowed" : "pointer"}`,
                    opacity: `${currentPage === 1 ? 0.5 : 1}`,
                  }}
                  onClick={() => {
                    setLastID(
                      source.length > 0 ? source[source.length - 1]._id : null
                    );
                    let currentPageTemp = currentPage;
                    currentPageTemp = currentPageTemp - 1;
                    setCurrentPage(currentPageTemp);
                    // setPrev(currentPageTemp * recordPerPage - recordPerPage + 1);
                    // setNext(
                    //   dataSource.length === recordPerPage
                    //     ? currentPageTemp * recordPerPage
                    //     : (currentPageTemp - 1) * recordPerPage + dataSource.length
                    // );
                    setFirstID(source.length > 0 ? source[0]._id : null);
                    reloadData(
                      listSearch,
                      source.length > 0 ? source[0]._id : null,
                      null,
                      sortBy
                    );
                  }}
                >
                  <img alt="" src={LeftPagi} />
                </div>
                <div
                  className="right-pagi"
                  style={{
                    pointerEvents: `${
                      source.length < recordPerPage ? "none" : ""
                    }`,
                    cursor: `${
                      source.length < recordPerPage ? "not-allowed" : "pointer"
                    }`,
                    opacity: `${source.length < recordPerPage ? 0.5 : 1}`,
                  }}
                  onClick={() => {
                    setLastID(
                      source.length > 0 ? source[source.length - 1]._id : null
                    );
                    let currentPageTemp = currentPage;
                    currentPageTemp = currentPageTemp + 1;
                    setCurrentPage(currentPageTemp);
                    setFirstID(source.length > 0 ? source[0]._id : null);
                    reloadData(
                      listSearch,
                      null,
                      source.length > 0 ? source[source.length - 1]._id : null,
                      sortBy
                    );
                  }}
                >
                  <img alt="" src={RightPagi} />
                </div>
              </NewCustomPagination>
            )}

            <WrapperSave>
              <CustomButtonSave
                onClick={() => {
                  handleSelect(selectedRowKeys);
                }}
                disabled={
                  rowData && rowData[field.field] !== null && selectedRowKeys[0]
                    ? false
                    : true
                }
              >
                Save
              </CustomButtonSave>
              <CustomButtonCancel
                onClick={() => {
                  setVisible(false);
                  setRowData({});
                }}
              >
                Cancel
              </CustomButtonCancel>
            </WrapperSave>
          </div>

          <TableWrap>
            {" "}
            {isLoadingDataLinking ? (
              <WrapperLoading>
                <img alt="" src={CRMLogo} />
                <div className="lds-ellipsis">
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                </div>
              </WrapperLoading>
            ) : (
              ""
            )}
            {renderTable()}
          </TableWrap>
        </Wrapper>
      </Drawer>
    </>
  );
};

export default DrawerLookup;

const WrapperSave = styled.div`
  position: fixed;
  bottom: -8px;
  right: 826px;
  text-align: right;
  background-color: #fff;
  height: 100px;
  padding-top: 16px;
  z-index: 3;
`;

const CustomButtonSave = styled(Button)`
  /* width: 80px; */
  background-color: ${(props) => props.theme.main};
  color: #fff;
  /* font-size: 16px; */
  background-color: ${(props) => props.theme.main};
  color: #fff;
  border-color: ${(props) => props.theme.main};
  margin-right: 16px;
  /* img {
    width: 15px;
    margin-right: 8px;
  } */
  &:hover {
    background-color: ${(props) => props.theme.darker};
    border-color: ${(props) => props.theme.darker};
    color: #fff;
    /* box-shadow: 0px 0px 16px rgba(62, 62, 62, 0.42);
    transition: 0.5s; */
  }
`;

const CustomButtonCancel = styled(Button)`
  /* font-size: 16px; */
  background-color: #fff;
  /* width: 80px; */
  color: #000;

  /* img {
    width: 15px;
    margin-right: 8px;
  } */
  &:hover {
    color: ${(props) => props.theme.darker};
    border-color: ${(props) => props.theme.darker};
    /* box-shadow: 0px 0px 16px rgba(62, 62, 62, 0.42);
    transition: 0.5s; */
  }
`;

const Wrapper = styled.div`
  .ant-form {
    position: absolute;
    top: 0;
    left: 126px;
    width: 320px;
  }
  table {
    width: max-content;
    th {
      width: max-content;
      .ant-table-column-title {
        margin-right: 30px;
      }
    }
    td {
      max-width: 200px;
    }
  }
  padding-bottom: 100px;
`;

const TableWrap = styled.div`
  width: 100%;
  /* height: calc(100% - 187px); */
  height: 100%;

  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  background: #fff;
  overflow-x: auto;
  overflow-y: auto;
  max-height: calc(100vh - 180px);
  &::-webkit-scrollbar {
    height: 8px !important;
  }

  table {
    /* width: 100%; */
    min-width: 100%;
    width: max-content;
    table-layout: auto;

    thead {
      position: sticky;
      top: 0;
      z-index: 3;
    }
    .table-header th {
      text-align: left;

      box-shadow: inset 1px 0px 0px #d9d9d9, inset 0px 1px 0px #d9d9d9,
        inset 0px -1px 0px #d9d9d9;

      overflow: hidden;
      resize: horizontal;
      background: #f0f0f0;
      min-width: 32px;
      white-space: nowrap;

      &:first-child {
        border-left: none;
        /* text-align: center; */
        width: 32px;
        max-width: 32px;
        resize: none;
        position: sticky;
        z-index: 4;
        left: 0;
        box-shadow: inset -1px 0px 0px #d9d9d9, inset 0px 1px 0px #d9d9d9,
          inset 0px -1px 0px #d9d9d9;
      }

      &:nth-child(2) {
        box-shadow: inset 0px 0px 0px #d9d9d9, inset 0px 1px 0px #d9d9d9,
          inset 0px -1px 0px #d9d9d9;
      }
    }

    .table-body td {
      border-bottom: 1px solid #ddd;
      padding: 16px;

      max-width: 200px;

      &:first-child {
        /* border-left: 1px solid #ddd; */
        width: 32px;
        max-width: 32px;
        padding: 0;
        text-align: center;
        position: sticky;
        z-index: 2;
        left: 0;

        filter: drop-shadow(-3px 3px 8px rgba(0, 0, 0, 0.15));
        .ant-radio-wrapper {
          margin-right: 0px;
        }

        .ant-radio-checked::after,
        .ant-radio-checked .ant-radio-inner {
          border-color: ${(props) => props.theme.main};
        }
      }
    }
  }
`;

const ContentHeader = styled.div`
  /* border-left: 1px solid #ddd;
  border-right: 1px solid #ddd; */
`;

const CustomSearch = styled(Input)`
  border-radius: 5px;
  &:hover,
  &:focus {
    border-color: ${(props) => props.theme.main};
    box-shadow: none;
  }
`;

const CustomSelect = styled(Select)`
  margin-bottom: 0px !important;

  .ant-select-selection-placeholder {
    font-weight: normal;
  }

  .ant-select-selector {
    border-radius: 5px !important;
  }
`;

const TitleWrap = styled.div`
  padding: 8px;
  display: flex;

  span {
    font-family: var(--roboto-500);
    font-size: 16px;
    color: #252424;
    font-weight: normal;
  }
`;

