import { Form, Input } from "antd";
import React from "react";

const Text = (props) => {
  const { field, autoFocus, style, disabled = false } = props;

  return (
    <Form.Item
      label={field.name}
      name={field.ID}
      rules={[
        {
          required: window.location.pathname.includes("create-template-record")
            ? false
            : field.required || field.soft_required,
          message: `Please input ${field.name}!`,
        },
      ]}
    >
      <Input
        autoFocus={autoFocus}
        maxLength={field.max_length}
        placeholder={field.placeholder}
        disabled={
          field.readonly ||
          (field.permission_read && field.permission_write === false) ||
          disabled
        }
        autoComplete={field.auto_fill ? "on" : "off"}
        style={{ ...style }}
      />
    </Form.Item>
  );
};

export default Text;
